import React, { usestate, useEffect } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import AppOnePage from "./AppOne";
import AppTwoPage from "./AppTwo";
import GA4React, { useGA4React } from "ga-4-react";
import "./App.css";

const ga4react = new GA4React("G-QC2JW90KEH");

function App() {
  const [ga, setGA] = usestate();

  //using useEffect to track pageviews with ga4react
  useEffect(() => {
    //initialize ga4react
    ga4react
      .initialize()
      .then((ga4) => {
        //track pageview
        ga4.pageview(window.location.pathname + window.location.search);
        ga4.gtag("event", "pageview", "path"); // or your custom gtag event
        setGA(ga4);
      })
      .catch((err) => console.log(err, "Analytics Failure."));
  }, []);

  const ClickHandler = () => {
    ga.gtag(
      "Button",
      "Click the button from first page",
      window.location.pathname + window.location.search
    );
    alert(
      "Send the information to google analystic that I touched the click button"
    );
    console.log("Event Button");
  };

  return (
    <div className="App">
      <header className="App-header">
        <button onClick={ClickHandler}>Click</button>
        <Router>
          <a href="/appOne">AppOne</a>
          <br />
          <a href="/appTwo">AppTwo</a>
          <br />
          <br />

          <Routes>
            <Route exact path="/appOne">
              <AppOnePage />{" "}
            </Route>
            <Route exact path="/appTwo">
              <AppTwoPage />{" "}
            </Route>
          </Routes>
        </Router>
      </header>
    </div>
  );
}

export default App;
